import React, { Component, Fragment } from "react";
import axios from "axios";
import { GrPowerReset } from "react-icons/gr";
import { IoIosArrowDown } from "react-icons/io";
import { MdAdd, MdClose } from "react-icons/md";
import { RiTodoLine } from "react-icons/ri";
import {
  AuthSchool,
  FC_AllSchoolHierarchyDetails,
  FC_GetSchoolLevels,
  FilteredCombination,
  HierarchyDataType,
  SchoolHierarchyInterface,
} from "../../actions";
import { APP_URL } from "../../config/app.config";
import { search } from "../../utils/functions";
import Loading from "../../components/Loading/Loading";
import { connect } from "react-redux";
import { Auth, Basic, SchoolHierarchyStore } from "../../actions";
import { StoreState } from "../../reducers";
import Dashboard from "../Dashboard/Dashboard";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import { Link } from "react-router-dom";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import EditNewForm from "./EditNewForm";

export const FilteredData = (
  school_hierarchy_data: SchoolHierarchyInterface[] | null,
  selected_level_code: string | null,
  selected_combination_code: string | null
): SchoolHierarchyInterface[] => {
  let res = school_hierarchy_data;
  if (res !== null) {
    if (selected_level_code !== null && selected_level_code !== "") {
      res = res.filter((itm) => itm.level_code === selected_level_code);
    }
    if (selected_combination_code !== null) {
      res = res.filter(
        (itm) => itm.combination_code === selected_combination_code
      );
    }
    return res;
  } else {
    return [];
  }
};

interface AddNewSubjectProps {
  schoolHierarchyStore: SchoolHierarchyStore;
  auth: Auth;
  basic: Basic;
  FC_AllSchoolHierarchyDetails: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
}
interface AddNewSubjectState {
  selected_level_code: string | null;
  filter_by_combination: boolean;
  selected_combination_code: string | null;
  search_comb: string;
  addedSubjects: SchoolHierarchyInterface[];
  loading: boolean;
  success: string;
  error: string;
  ediSubject: SchoolHierarchyInterface | null;
}

class _SubjectsList extends Component<AddNewSubjectProps, AddNewSubjectState> {
  constructor(props: AddNewSubjectProps) {
    super(props);

    this.state = {
      selected_level_code: null,
      filter_by_combination: false,
      selected_combination_code: null,
      search_comb: "",
      addedSubjects: [],
      loading: false,
      success: "",
      error: "",
      ediSubject: null,
    };
  }

  componentDidMount = () => {
    if (this.props.schoolHierarchyStore.hierarchy === null) {
      this.setState({ loading: true });
      this.props.FC_AllSchoolHierarchyDetails(
        (status: boolean, msg: string) => {
          if (status === true) {
            this.setState({ loading: false });
          }
        }
      );
    }
  };

  render() {
    return (
      <Fragment>
        <DashboardContainer>
          <div className="bg-white rounded-md z-50 w-full h-full overflow-auto">
            <div className="rounded-t-md flex flex-row items-center justify-between gap-4 py-2 border-b border-gray-400">
              <div className="text-xl font-bold ml-2 uppercase mt-2">
                List of subjects (
                {this.props.schoolHierarchyStore.hierarchy?.length})
              </div>
              <Link
                to="/hierarchy-add-subject"
                className="flex items-center justify-center py-2 px-3 rounded bg-gray-100 text-2xl hover:bg-blue-100 hover:text-primary-800 cursor-pointer"
                title="Close window"
              >
                <MdAdd />
                <span className="text-base">Add subject</span>
              </Link>
            </div>
            <div className="">
              <div className="">
                <div className="mb-10">
                  <div>
                    <div className="mb-5">
                      <div className="mt-4">
                        <div>Filter by</div>
                        <div className="flex flex-row items-center gap-3">
                          <select
                            onChange={(e) =>
                              this.setState({
                                selected_level_code: e.target.value,
                              })
                            }
                            value={
                              this.state.selected_level_code === null
                                ? ""
                                : this.state.selected_level_code
                            }
                            className="border border-gray-400 rounded px-3 py-2"
                          >
                            <option value="">Choose education level</option>
                            <option value="">Show all levels</option>
                            {this.props.schoolHierarchyStore.hierarchy &&
                              FC_GetSchoolLevels(
                                this.props.schoolHierarchyStore.hierarchy,
                                {
                                  type: HierarchyDataType.SCHOOL_LEVELS,
                                  status: true,
                                }
                              ).levels.map((item, i) => (
                                <option key={i + 1} value={item.level_code}>
                                  {item.level_name}
                                </option>
                              ))}
                          </select>
                          <div>
                            <div
                              onClick={() =>
                                this.setState({
                                  filter_by_combination:
                                    !this.state.filter_by_combination,
                                })
                              }
                              className="w-auto border border-gray-400 rounded px-4 py-2 bg-white flex flex-row items-center justify-between gap-10 cursor-pointer hover:border-accent-900 hover:text-accent-900"
                            >
                              {this.state.selected_combination_code === null ? (
                                <span>All- Click to filter by combination</span>
                              ) : (
                                <span>
                                  {this.props.schoolHierarchyStore.hierarchy &&
                                    FC_GetSchoolLevels(
                                      this.props.schoolHierarchyStore.hierarchy,
                                      {
                                        type: HierarchyDataType.COMBINATIONS,
                                        status: true,
                                      }
                                    ).combinations.find(
                                      (itm) =>
                                        itm.combination_code ===
                                        this.state.selected_combination_code
                                    )?.combination_name}
                                </span>
                              )}
                              <div className="-mr-3">
                                {this.state.filter_by_combination === true ? (
                                  <MdClose />
                                ) : (
                                  <IoIosArrowDown />
                                )}
                              </div>
                            </div>
                            {this.state.filter_by_combination === true && (
                              <div className="absolute w-max bg-white px-3 py-3 rounded-b animate__animated animate__zoomIn animate__faster shadow-xl">
                                <div>
                                  <div className="flex flex-row items-center w-full justify-between gap-2">
                                    <input
                                      value={this.state.search_comb}
                                      onChange={(e) =>
                                        this.setState({
                                          search_comb: e.target.value,
                                        })
                                      }
                                      placeholder="Search..."
                                      type="text"
                                      className="border border-gray-400 rounded px-3 py-2 text-sm w-full"
                                    />
                                    <div>
                                      <div
                                        onClick={() =>
                                          this.setState({
                                            filter_by_combination: false,
                                          })
                                        }
                                        className="bg-red-100 text-red-800 text-2xl p-1 rounded-full cursor-pointer hover:bg-red-600 hover:text-white"
                                      >
                                        <MdClose />
                                      </div>
                                    </div>
                                  </div>
                                  {this.state.selected_combination_code !==
                                    null && (
                                    <div
                                      onClick={() => {
                                        this.setState({
                                          selected_combination_code: null,
                                          filter_by_combination: false,
                                        });
                                      }}
                                      className="flex flex-row items-center gap-3 text-gray-800 font-bold cursor-pointer hover:text-accent-900 px-3 py-1 my-2 rounded bg-gray-100 w-full"
                                    >
                                      <div>
                                        <GrPowerReset className="text-xl" />
                                      </div>
                                      <span>Click to view all</span>
                                    </div>
                                  )}
                                </div>
                                <div
                                  className="h-64 overflow-y-auto mt-1 animate__animated animate__fadeIn"
                                  style={{ minWidth: "28rem" }}
                                >
                                  {search(
                                    this.props.schoolHierarchyStore.hierarchy &&
                                      FC_GetSchoolLevels(
                                        this.props.schoolHierarchyStore
                                          .hierarchy,
                                        {
                                          type: HierarchyDataType.COMBINATIONS,
                                          status: true,
                                        }
                                      ).combinations,
                                    this.state.search_comb
                                  ).length === 0 ? (
                                    <div className="text-center text-lg font-bold p-5 px-10 text-gray-500">
                                      No result found!
                                    </div>
                                  ) : (
                                    this.props.schoolHierarchyStore.hierarchy &&
                                    search(
                                      FC_GetSchoolLevels(
                                        this.props.schoolHierarchyStore
                                          .hierarchy,
                                        {
                                          type: HierarchyDataType.COMBINATIONS,
                                          status: true,
                                        }
                                      ).combinations,
                                      this.state.search_comb
                                    ).map(
                                      (
                                        item: FilteredCombination,
                                        i: number
                                      ) => (
                                        <div
                                          key={i + 1}
                                          className={`w-full flex flex-row items-center border-b ${
                                            this.state
                                              .selected_combination_code ===
                                            item.combination_code
                                              ? "border-yellow-100 bg-yellow-100 font-bold"
                                              : "border-gray-200"
                                          } py-1 gap-4 hover:bg-yellow-100 cursor-pointer`}
                                          onClick={() => {
                                            this.setState({
                                              selected_combination_code:
                                                item.combination_code,
                                              filter_by_combination: false,
                                            });
                                          }}
                                        >
                                          <div>
                                            <div className="rounded-full bg-yellow-50 border border-yellow-200 h-8 w-8 flex flex-row items-center justify-center font-bold">
                                              {i + 1}
                                            </div>
                                          </div>
                                          <div>{item.combination_name}</div>
                                        </div>
                                      )
                                    )
                                  )}
                                </div>
                              </div>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="bg-white rounded border border-gray-200">
                      <div className="w-full overflow-x-auto">
                        <table className="min-w-full text-left">
                          <thead>
                            <tr className="font-semibold border-b text-base">
                              <th className="p-2 border-b-2 border-gray-300 text-center">
                                #
                              </th>
                              <th className="p-2 border-b-2 border-gray-300">
                                Subject
                              </th>
                              <th className="p-2 border-b-2 border-gray-300">
                                Level
                              </th>
                              <th className="p-2 border-b-2 border-gray-300">
                                Combination
                              </th>
                              <th className="p-2 border-b-2 border-gray-300">
                                Grade
                              </th>
                              <th className="p-2 border-b-2 border-gray-300">
                                Periods
                              </th>
                              <th className="p-2 border-b-2 border-gray-300 text-right">
                                Action
                              </th>
                            </tr>
                          </thead>
                          <tbody className="text-sm">
                            {this.props.schoolHierarchyStore.hierarchy ===
                            null ? (
                              <tr>
                                <td colSpan={7}>
                                  <div className="flex w-full items-center justify-center py-3">
                                    <Loading />
                                  </div>
                                </td>
                              </tr>
                            ) : FilteredData(
                                this.props.schoolHierarchyStore.hierarchy,
                                this.state.selected_level_code,
                                this.state.selected_combination_code
                              ).length === 0 ? (
                              <td colSpan={7}>
                                <div className="text-lg p-3 text-center">
                                  No result found
                                </div>
                              </td>
                            ) : (
                              FilteredData(
                                this.props.schoolHierarchyStore.hierarchy,
                                this.state.selected_level_code,
                                this.state.selected_combination_code
                              ).map((item, i) => (
                                <tr
                                  key={i + 1}
                                  className={`text-gray-500 group`}
                                >
                                  <td className="p-2 border-b text-center">
                                    {i + 1}
                                  </td>
                                  <td className="p-2 border-b">
                                    {item.course_code}-{item.course_name}
                                  </td>
                                  <td className="p-2 border-b">
                                    {item.level_name}
                                  </td>
                                  <td className="p-2 border-b">
                                    {item.combination_name}
                                  </td>
                                  <td className="p-2 border-b">
                                    {item.grade_name}
                                  </td>
                                  <td className="p-2 border-b">
                                    {item.teaching_period}
                                  </td>
                                  <td className="p-2 border-b">
                                    <div className="flex flex-row items-center justify-end">
                                      <div
                                        onClick={() =>
                                          this.setState({ ediSubject: item })
                                        }
                                        className="px-3 py-1 rounded bg-blue-100 group-hover:bg-primary-800 cursor-pointer text-black font-bold group-hover:text-white"
                                      >
                                        Edit
                                      </div>
                                    </div>
                                  </td>
                                </tr>
                              ))
                            )}
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </DashboardContainer>
        {this.state.ediSubject !== null && (
          <Modal
            title="Update subject"
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ ediSubject: null })}
            backDropClose={true}
            widthSizeClass={ModalSize.medium}
            displayClose={true}
            padding={{
              title: true,
              body: false,
            }}
          >
            <div className="border-t border-gray-400 px-3">
              <EditNewForm
                data={this.state.ediSubject}
                reload={() => {
                  this.setState({ loading: true });
                  this.props.FC_AllSchoolHierarchyDetails(
                    (status: boolean, msg: string) => {
                      if (status === true) {
                        this.setState({ loading: false });
                      }
                    }
                  );
                }}
              />
            </div>
          </Modal>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  schoolHierarchyStore,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  schoolHierarchyStore: SchoolHierarchyStore;
} => {
  return {
    auth: auth,
    basic: basic,
    schoolHierarchyStore: schoolHierarchyStore,
  };
};

export const SubjectsList = connect(mapStateToProps, {
  FC_AllSchoolHierarchyDetails,
})(_SubjectsList);
