import axios from "axios";
import React, { useState } from "react";
import { SchoolHierarchyInterface } from "../../actions/study_hierarchy.action";
import { APP_URL } from "../../config/app.config";
import { setAxiosToken } from "../../utils/AxiosToken";
import { errorToText } from "../../utils/errors";

interface AddNewFormProps {
  data: SchoolHierarchyInterface;
  reload: () => void;
}

export default function EditNewForm(props: AddNewFormProps) {
  const [course_code, setCourseCode] = useState<string>(props.data.course_code);
  const [course_name, setCourseName] = useState<string>(props.data.course_name);
  const [periods, setPeriods] = useState<string>(props.data.teaching_period);
  const [success, setSuccess] = useState<string>("");
  const [loading, setLoading] = useState<boolean>(false);
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const editSubject = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setFormError(null);
    if (course_code === "") {
      return setFormError({
        element: "course_code",
        msg: "Please fill course code",
      });
    }
    if (course_name === "") {
      return setFormError({
        element: "course_name",
        msg: "Please fill course name",
      });
    }
    if (periods === "") {
      return setFormError({
        element: "periods",
        msg: "Please fill teaching periods",
      });
    }
    setLoading(true);
    setAxiosToken();
    try {
      const res = await axios.post(`${APP_URL}/hierarch/upperiod`, {
        studyhierarchyid: props.data.studyhierarchyid,
        course_code: course_code,
        course_name: course_name,
        teaching_period: parseInt(periods),
      });
      if (res.status === 200) {
        props.reload();
        setLoading(false);
        setSuccess("Subject updated successfully!");
      }
    } catch (error: any) {
      console.log("Erro: ", { ...error });
      setFormError({
        element: "main",
        msg: errorToText(error),
      });
      setLoading(false);
    }
  };
  return (
    <div className="py-3">
      <div className="bg-blue-100 rounded p-3 flex flex-col gap-2 mb-3">
        <div className="flex flex-row items-center gap-3">
          <span className="text-gray-800">School level:</span>
          <span className="text-primary-800 font-bold">
            ({props.data.level_code}) {props.data.level_name}
          </span>
        </div>
        <div className="flex flex-row items-center gap-3">
          <span className="text-gray-800">Combination:</span>
          <span className="text-primary-800 font-bold">
            ({props.data.combination_code}) {props.data.combination_name}
          </span>
        </div>
        <div className="flex flex-row items-center gap-3">
          <span className="text-gray-800">Grade:</span>
          <span className="text-primary-800 font-bold">
            ({props.data.grade_code}) {props.data.grade_name}
          </span>
        </div>
      </div>
      <form onSubmit={editSubject}>
        <div className="flex flex-col mb-3">
          <span>Course code</span>
          <input
            type="text"
            value={course_code}
            onChange={(e) => {
              setCourseCode(e.target.value);
            }}
            className={`border ${
              formError !== null && formError.element === "course_code"
                ? "border-red-600"
                : "border-gray-300"
            } px-3 py-2 w-full rounded`}
            placeholder="Fill course code"
            disabled={loading}
          />
          {formError !== null && formError.element === "course_code" && (
            <div className="text-red-600 font-bold text-base">
              {formError.msg}
            </div>
          )}
        </div>
        <div className="flex flex-col mb-3">
          <span>Course name</span>
          <input
            type="text"
            value={course_name}
            onChange={(e) => {
              setCourseName(e.target.value);
            }}
            className={`border ${
              formError !== null && formError.element === "course_name"
                ? "border-red-600"
                : "border-gray-300"
            } px-3 py-2 w-full rounded`}
            placeholder="Fill course name"
            disabled={loading}
          />
          {formError !== null && formError.element === "course_name" && (
            <div className="text-red-600 font-bold text-base">
              {formError.msg}
            </div>
          )}
        </div>
        <div className="flex flex-col mb-3">
          <span>Periods</span>
          <input
            type="number"
            value={periods}
            onChange={(e) => {
              setPeriods(e.target.value);
            }}
            className={`border ${
              formError !== null && formError.element === "periods"
                ? "border-red-600"
                : "border-gray-300"
            } px-3 py-2 w-full rounded`}
            placeholder="Fill number of periods"
            disabled={loading}
          />
          {formError !== null && formError.element === "periods" && (
            <div className="text-red-600 font-bold text-base">
              {formError.msg}
            </div>
          )}
        </div>
        {formError !== null && formError.element === "main" && (
          <div
            onClick={() => setFormError(null)}
            className="cursor-pointer text-red-600 font-bold text-base bg-red-100 rounded px-3 py-2"
          >
            {formError.msg}
          </div>
        )}
        {success !== "" && (
          <div
            onClick={() => setSuccess("")}
            className="cursor-pointer text-green-600 font-bold text-base bg-green-100 rounded px-3 py-2"
          >
            {success}
          </div>
        )}
        <div className="flex flex-row justify-between items-center gap-3 mb-3 mt-3">
          {/* <div className="flex items-center justify-center px-3 py-2 rounded cursor-pointer bg-gray-100 text-black font-bold hover:bg-gray-200">
            Cancel
          </div> */}
          <div></div>
          <button
            type="submit"
            className="flex items-center justify-center px-3 py-2 rounded cursor-pointer bg-primary-800 text-white font-bold hover:bg-blue-500"
            disabled={loading}
          >
            Update subject
          </button>
        </div>
      </form>
    </div>
  );
}
